import React, {useState} from 'react';
import {fs, storge} from '../firebase';
//import getDownloadURL from 'firebase/storage';
import '../addprod.css';
export const AddProducts= ()=>{

    const [title, setTitle]= useState("");
    const [description, setDescription]= useState("");
    const [price, setPrice]= useState("");
    const [image, setImage]=useState();

    const [imageError, setImageError]=useState("");
    const [successMsg, setSuccessMsg]=useState("");
    const [uploadError, setUploadError]=useState("");

    
    const types=['image/jpg', 'image/jpeg', 'image/png', 'image/PNG'];
    const handleProductImg=(e)=>{
        let selectedFile = e.target.files[0];
    if(selectedFile){
        if(selectedFile&&types.includes(selectedFile.type)){
            setImage(selectedFile);
            setImageError('');
        
        }
        else{
            setImage(null);
            setImageError('please select a valid image file type (png or jpg)')
        }

    }
    else{
        console.log('please select your file');
    }
    }

    const handleAddProducts=(e)=>{
        e.preventDefault();
        // console.log(title, description, price);
        // console.log(image);
            
        const uploadTask= storge.ref(`product-images/${image.name}`).put(image);
        uploadTask.on('state_changed',snapshot=>{
            const progress =(snapshot.bytesTransferred/snapshot.totalBytes)*100
            console.log(progress);
        },error=>setUploadError(error.message),()=>{
            storge
            .ref('product-images')
            .child(image.name)
            .getDownloadURL()
            .then(url=>{
                fs.collection('Products').add({
                    title,
                    description,
                    price: Number(price),
                    url
                }).then(()=>{
                    setSuccessMsg('Product added Successfully');
                    setTitle('');
                    setDescription('');
                    setPrice('');
                    document.getElementById('file').value='';
                    setImageError("");
                    setUploadError('');
                    setTimeout(()=>{
                        setSuccessMsg('');
                    },3000)
                }).catch(error=>setUploadError(error.message));
            })
        })
    }
   
return(
<div className='container'>
<div className='labels'><h1 style={{color:'rgba(179, 153, 119, 0.94)'}}>Weddire</h1></div>
            <br></br>
            <br></br>
            <h1 style={{display:'flex', justifyContent:'right', fontSize:'40px'}}>Add Products</h1>
                   
            {successMsg&&<>
                <div className='success-msg'>{successMsg}</div>
                <br></br>
            </>} 
            <div className='fcontainer'>
            <form autoComplete="off" className='form-group' onSubmit={handleAddProducts}>
                <label className='lb'>Product Title</label>
                <input type="text" style={{marginLeft:'135px'}} className='form-control' required
                onChange={(e)=>setTitle(e.target.value)} value={title}></input>
                <br></br>
                <label className='lb' >Product Description</label>
                <input type="text" style={{marginLeft:'60px'}} className='form-control' required
                onChange={(e)=>setDescription(e.target.value)} value={description}></input>
                <br></br>
                <label className='lb'>Product Price</label>
                <input type="number" style={{marginLeft:'131px'}} className='form-control' required
                onChange={(e)=>setPrice(e.target.value)} value={price}></input>
                <br></br>
                <label className='lb'>Upload Product Image</label>
                <input type="file" id="file" className='form-control' required
                onChange={handleProductImg}></input>
                
                {imageError&&<>
                    <br></br>
                    <div className='error-msg'>{imageError}</div>
                   
                </>}
                <br></br>           
                <div>
                    <button type="submit" className='btn btn-success btn-md'>
                        SUBMIT
                    </button>
                </div>
            </form></div>
            {uploadError&&<>
                    <br></br>
                    <div className='error-msg'>{uploadError}</div>
                </>}

        </div>
)}
export default AddProducts;