import React, { useEffect, useState } from "react";
import { Link, useNavigate } from "react-router-dom";
import { auth, signInWithEmailAndPassword, signInWithGoogle } from "../firebase";
import { useAuthState } from "react-firebase-hooks/auth";
import "../login.css";
import styled from 'styled-components';
const StyledLink = styled(Link)`
    text-decoration: none;

    &:focus, &:hover, &:visited, &:link, &:active {
        text-decoration: none;
    }
`;
function Login() {
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [user, loading, error] = useAuthState(auth);
  const navigate = useNavigate();
  useEffect(() => {
    if (loading) {
      // maybe trigger a loading screen
      return;
    }
    if (user) navigate("/dashboard");
  }, [user, loading]);
  return (
    <div className="login">
      <div className="login__container">
        <input
          type="text"
          className="login__textBox"
          value={email}
          onChange={(e) => setEmail(e.target.value)}
          placeholder="E-mail Address"
        />
        <input
          type="password"
          className="login__textBox"
          value={password}
          onChange={(e) => setPassword(e.target.value)}
          placeholder="Password"
        />
        <button
          className="login__btn"
          component={Link}
          onClick={() => signInWithEmailAndPassword(email, password)}
        >
          <Link to="/dashboard"  style={{textDecoration:"none", color: 'white'}} >Login</Link>
        </button>
        
        <button className="login__btn login__google"
        component={Link}
          onClick={signInWithGoogle}>
          <Link to="/dashboard"  style={{textDecoration:"none", color: 'white'}} >Login with Google</Link>
        </button>
        <div className="forgot">
          <Link to="/reset" style={{color:'black'}}>Forgot Password</Link>
        </div>
        <div>
          Don't have an account? <Link to="/register" style={{color:'black'}}>
          Register</Link> now.
        </div>
      </div>
    </div>
  );
}
export default Login;